:root {
  --white: #ffffff;
  --green: #5F8D6E;
  --light-gray: #F3F3F3;
  --dark-gray: #5F5F5F;
  --mid-grey: #8d8d8d;
  --gray2: #4f4f4f;
  --orange: #E8A05E;
  --bege:#F0EEE0;
  --bege2: #f9f8f3;
  --background: #F5F5F5;
  --background-gray: #fafafa;
  --lirear: rgb(99, 158, 117);
  --large-screen: 1472px;
  --tablet-screen: 740px;
  --mobile-screen: 320px;
  --padding: 16px;
}

@font-face {
  font-family: "Elisabethische";
  src: local("Elisabethische"), url("../fonts/Elisabethische.woff2") format("woff2"), url("../fonts/Elisabethische.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  src: local("Rubik-Italic"), url("../fonts/Rubik-Italic.woff2") format("woff2"), url("../fonts/Rubik-Italic.woff") format("woff");
  font-weight: 400;
  font-style: "italic";
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  src: local("Rubik-Regular"), url("../fonts/Rubik-Regular.woff2") format("woff2"), url("../fonts/Rubik-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  src: local("Rubik-Medium"), url("../fonts/Rubik-Medium.woff2") format("woff2"), url("../fonts/Rubik-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Rubik";
  src: local("Rubik-Light"), url("../fonts/Rubik-Light.woff2") format("woff2"), url("../fonts/Rubik-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
.container {
  width: 100%;
  padding-left: var(--padding);
  padding-right: var(--padding);
  margin: 0 auto;
}
@media (min-width: 1472px) {
  .container {
    max-width: var(--large-screen);
  }
}

.container-inside {
  padding-top: 36px;
  padding-bottom: 36px;
}

@media (min-width: 1472px) {
  .container-inside {
    padding-bottom: 50px;
  }
}
.btn {
  border: 0;
  padding: 0;
  margin: 0;
  background-color: transparent;
  border: 0;
  font-family: inherit;
  cursor: pointer;
}

html {
  min-height: 100vh;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  font-size: 16px;
  line-height: 130%;
  color: var(--dark-gray);
  font-family: "Rubik", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
  background-color: var(--white);
}

main {
  flex-grow: 1;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

input {
  margin: 0;
  padding: 0;
  background-color: transparent;
  font-size: inherit;
  font-family: inherit;
  color: var(--dark-gray);
  cursor: pointer;
}
input:hover, input:focus {
  outline: none;
}

.visually-hidden:not(:focus) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.h1 {
  margin: 0;
  font-family: Elisabethische, serif;
  font-style: normal;
  font-weight: 400;
  font-size: 2.25rem;
  line-height: 130%;
  text-transform: uppercase;
  color: var(--dark-gray);
}

.h2 {
  margin: 0;
  position: relative;
  font-family: Elisabethische, serif;
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 130%;
  text-transform: uppercase;
  color: var(--dark-gray);
}

.h2__wrapper {
  display: flex;
  justify-content: flex-start;
  padding-bottom: 6px;
}

.h2--underlined,
.h2--short {
  margin-bottom: 36px;
}
.h2--underlined::before,
.h2--short::before {
  content: "";
  position: absolute;
  bottom: -6px;
  right: 0;
  display: block;
  height: 1px;
  width: calc(100% + var(--padding));
  background-color: var(--dark-gray);
}

.list {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.list--info li {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  padding-left: 28px;
}
.list--info li::before {
  content: "";
  position: absolute;
  top: -2px;
  left: 0;
  width: 24px;
  height: 24px;
  background-image: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
}

.phone-link {
  font-weight: 500;
  font-size: 1rem;
  line-height: 1.2;
  color: var(--dark-gray);
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

@media (min-width: 740px) {
  .h1 {
    font-size: 3rem;
  }

  .h2 {
    font-size: 2.25rem;
  }
}
@media (min-width: 1472px) {
  .h1 {
    font-size: 3.75rem;
  }

  .h2--underlined::before {
    width: 500%;
  }

  .h2--short {
    padding-top: 43px;
  }
  .h2--short::before {
    width: 100%;
  }

  .h2--star {
    padding-top: 43px;
  }
  .h2--star::after {
    content: "";
    position: absolute;
    right: 100%;
    top: 0;
    display: block;
    width: 60px;
    height: 60px;
    background-image: url(../img/star.svg);
    background-position: center;
    background-repeat: no-repeat;
  }
}
.main {
  padding-bottom: 50px;
}

.hero-slider__title {
  line-height: 1.4;
}
.hero-slider__title span {
  display: block;
}

.section-head h2 {
  line-height: 1.4;
}

button {
  appearance: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  border: none;
  font: inherit;
  background-color: transparent;
  line-height: 1;
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  box-sizing: border-box;
}

.btn--primary,
.btn--secondary {
  height: 50px;
  line-height: 1;
  font-size: 12px;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.2s ease;
}
.btn--primary:hover,
.btn--secondary:hover {
  text-decoration: none;
}

.btn--secondary {
  background-color: transparent;
  color: var(--dark-gray);
  border: 1px solid;
  padding-left: 24px;
  padding-right: 24px;
  transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}
.btn--secondary:hover {
  border-color: var(--green);
  color: var(--green);
}

.btn--icon {
  font-size: 0;
  line-height: 1;
}
.btn--icon:hover {
  opacity: 0.8;
}

.btn--primary {
  background-color: var(--orange);
  color: var(--white);
  border: 1px solid var(--orange);
  padding-left: 13px;
  padding-right: 13px;
  position: relative;
}
.btn--primary:hover {
  background-color: #dd8c42;
}
.btn--primary::after {
  content: "";
  position: absolute;
  top: 3px;
  bottom: 3px;
  left: 3px;
  right: 3px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  pointer-events: none;
}

.btn--green {
  padding-right: 24px;
  padding-left: 24px;
  height: 50px;
  width: fit-content;
  font-weight: 700;
  font-size: 0.75rem;
  line-height: 1;
  background-color: transparent;
  color: var(--dark-gray);
  border: 1px solid var(--dark-gray);
  white-space: nowrap;
  text-decoration: none;
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}
.btn--green:hover, .btn--green:focus {
  color: var(--green);
  text-decoration: none;
  border-color: var(--green);
}

.btn--green.btn--active {
  color: var(--white);
  background-color: var(--green);
  border-color: var(--green);
}

