.flatpickr-time input:focus {
  outline: 0;
  border: 0;
}

.flatpickr-time .flatpickr-time-separator,
.flatpickr-time .flatpickr-am-pm {
  height: inherit;
  float: left;
  line-height: inherit;
  color: #393939;
  font-weight: bold;
  width: 2%;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-align-self: center;
  -ms-flex-item-align: center;
  align-self: center;
}

.flatpickr-time .flatpickr-am-pm {
  outline: 0;
  width: 18%;
  cursor: pointer;
  text-align: center;
  font-weight: 400;
}

.flatpickr-time input:hover,
.flatpickr-time .flatpickr-am-pm:hover,
.flatpickr-time input:focus,
.flatpickr-time .flatpickr-am-pm:focus {
  background: #eee;
}

.flatpickr-input[readonly] {
  cursor: pointer;
}

@-webkit-keyframes fpFadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -20px, 0);
    transform: translate3d(0, -20px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
@keyframes fpFadeInDown {
  from {
    opacity: 0;
    -webkit-transform: translate3d(0, -20px, 0);
    transform: translate3d(0, -20px, 0);
  }
  to {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}
.text {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}
.text p {
  margin: 0;
  font-size: 1rem;
  line-height: 140%;
}
.text ul {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
}
.text ul li {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
  padding-left: 28px;
}
.text ul li::before {
  content: "";
  position: absolute;
  top: -2px;
  left: 0;
  width: 24px;
  height: 24px;
  background-image: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(data:image/svg+xml;base64,);
}
.text ul + p {
  padding-top: 8px;
}
.text ol {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 16px;
  margin: 0;
  padding: 0;
  font-size: 0.875rem;
  list-style: none;
  counter-reset: li;
}
.text ol li {
  position: relative;
  margin: 0;
  padding: 0;
}
.text ol li:before {
  content: counter(li) ". ";
  counter-increment: li;
  padding-right: 8px;
  font-weight: 700;
  color: var(--green);
}
.text h2 {
  position: relative;
  margin-bottom: 12px;
  font-family: Elisabethische, serif;
  font-weight: 400;
  font-size: 1.5rem;
  line-height: 130%;
  text-transform: uppercase;
  color: var(--dark-gray);
}
.text h2::before {
  content: "";
  position: absolute;
  bottom: -6px;
  right: 0;
  display: block;
  height: 1px;
  width: calc(100% + var(--padding));
  background-color: var(--dark-gray);
}
.text blockquote {
  position: relative;
  margin: 0;
  padding-left: 60px;
  padding-top: 16px;
  padding-right: 16px;
  padding-bottom: 16px;
  min-height: 105px;
  font-style: italic;
  font-weight: 400;
  font-size: 0.875rem;
  line-height: 140%;
  color: var(--gray2);
  background-color: var(--bege);
}
.text blockquote::before {
  content: "";
  position: absolute;
  left: 16px;
  top: 16px;
  display: block;
  width: 28px;
  height: 73px;
  background-image: url("../img/quote.svg");
  background-repeat: no-repeat;
  background-size: cover;
}
.text h3 {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 500;
  font-size: 1.125rem;
  line-height: 140%;
  color: var(--gray2);
}
.text h3 + p {
  margin-top: -8px;
}
.text img {
  width: 100%;
  height: auto;
}
.text .bg-quote {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  gap: 0;
}

.text--three-columns {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 36px;
}
.text--three-columns div {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 24px;
  align-items: flex-start;
  align-content: flex-start;
}

.gray {
  background-color: var(--light-gray);
}
.gray .text blockquote {
  margin-top: 20px;
  background-color: var(--white);
}

@media (min-width: 740px) {
  .text p {
    font-size: 1.185rem;
  }
  .text h3 {
    font-size: 1.25rem;
  }
  .text blockquote {
    padding-left: 100px;
    padding-top: 36px;
    padding-bottom: 36px;
    padding-right: 36px;
    min-height: 130px;
  }
  .text blockquote::before {
    top: 36px;
    left: 36px;
  }
  .text ol {
    font-size: 1rem;
  }
  .text .h2 {
    font-size: 2.25rem;
  }
  .text .bg-quote {
    position: relative;
    align-content: flex-end;
    padding: 32px;
    overflow: hidden;
  }
  .text .bg-quote img {
    position: absolute;
    top: 50%;
    left: 50%;
    min-height: 100%;
    min-width: 100%;
    object-fit: cover;
    transform: translate(-50%, -50%);
  }

  .text--three-columns {
    display: flex;
    column-gap: 24px;
    flex-wrap: wrap;
    justify-content: center;
  }
  .text--three-columns div {
    flex-basis: calc(50% - 12px);
  }
}
@media (min-width: 1000px) {
  .text--three-columns div {
    flex-basis: calc(33.3333333333% - 16px);
    flex-grow: 1;
  }
}
@media (min-width: 1472px) {
  .text h2::before {
    width: 500%;
  }
  .text h2 {
    padding-top: 43px;
  }
  .text h2::after {
    content: "";
    position: absolute;
    right: 100%;
    top: 0;
    display: block;
    width: 60px;
    height: 60px;
    background-image: url(../img/star.svg);
    background-position: center;
    background-repeat: no-repeat;
  }

  .text--three-columns {
    column-gap: 44px;
  }
  .text--three-columns div {
    flex-basis: calc(33.3333333333% - 30px);
    flex-grow: 1;
  }
}